body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  font-size: calc(8px + .5vw);
}

@font-face {
  font-family: 'Simplifica';
  src: url(/colour-flood/static/media/Simplifica.22dd36f6.ttf) format('truetype');
}

body {
  display: flex;
  justify-content: center;
  padding-top: 4rem;
}

.game {
  border: 5px solid lightgrey;
  padding: 1rem;
}

aside {
  font-family: 'Simplifica', 'Wire One', sans-serif;
  padding-top: .4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 1rem;
}

h1, button {
  font-size: 3rem;
  text-align: center;
  background: none;
  font-weight: bold;
  font-family: 'Simplifica', 'Wire One', sans-serif;
}

h1 {
  margin: 0 0 1rem;
}

.score {
  font-size: 10rem;
  width: 7rem;
  text-align: center;
  margin: 0 0 0 .5rem;
  padding: 0;
}

.flex {
  display: flex;
}

.board {
  width: 35rem;
  height: 35rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.square {
  width: 100%;
  height: 100%;
  margin: .1rem;
  border-radius: 15px;
  box-sizing: border-box;
}

.row {
  display: flex;
  margin: .1rem;
  width: 100%;
  height: 100%;
}

.controls-container {
  width: 100%;
  display: flex;
  padding: 1rem;
  box-sizing: border-box;
  justify-content: space-between;
}

.button {
  width: 4rem;
  height: 4rem;
  border-radius: 2rem;
  box-sizing: border-box;
}

.reset {
  padding: .5rem 1rem 0 1rem;
}

button:focus {
  outline: none;
}

.colour0 {
  background: rgb(255, 153, 153);
  border: 4px solid rgb(204, 0, 0);
}

.colour1 {
  background: rgb(153, 255, 153);
  border: 4px solid rgb(0, 204, 0);
}

.colour2 {
  background: rgb(153, 153, 255);
  border: 4px solid rgb(0, 0, 204);
}

.colour3 {
  background: rgb(255, 255, 153);
  border: 4px solid rgb(204, 204, 0);
}

.colour4 {
  background: rgb(153, 255, 255);
  border: 4px solid rgb(0, 204, 204);
}

.colour5 {
  background: rgb(255, 153, 255);
  border: 4px solid rgb(204, 0, 204);
}

.difficulty h1 {
  margin-top: 1rem;
}

.difficulty button {
  font-size: 1rem;
  background: none;
  border-radius: 1rem;
  border: 1px solid lightgrey;
  height: 2rem;
  width: 2rem;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: .3rem;
}

.difficulty button:active {
  border: none;
}

